import { Trans } from '@lingui/macro'
import { CircularProgress } from '@material-ui/core'
import { Check, Warning } from '@material-ui/icons'
import { ApolloQueryResult } from 'apollo-client'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { useApolloClient } from 'react-apollo-hooks'
import styled from 'styled-components'

import { Flex } from '../../../atoms/Flex'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { useRoot } from '../../../atoms/RootProvider'
import { GValidateLocation } from '../../../graph/types'
import { i18n } from '../../../i18n'
import {
  AddressSuggest,
  IAddressWithGps,
} from '../../../molecules/AddressSuggest'
import { Colors } from '../../../styling'
import { ValidateLocationQuery } from '../models/CreateOrderModel.graphql'

interface IProps {
  isVisible: boolean
}

const InputLine = styled(FlexRowSingle)`
  justify-content: flex-start;
`

const ZoneWarning = styled.div`
  display: inline-flex;
  align-items: center;
  color: ${Colors.violet};
`

const AddressValid = styled.div`
  color: ${Colors.green};
`

export const OrderFormDeliveryAddress = observer<IProps>(props => {
  const { createOrder } = useRoot()
  const [status, setStatus] = React.useState<ReactNode>(null)

  const validateAddress = useAddressValidation(createOrder.currentBranch.id)

  const onSuggestSelect = async (selected: IAddressWithGps) => {
    createOrder.useLocation(selected)
    setStatus(<CircularProgress size={20} />)
    let isValid
    try {
      isValid = await validateAddress(selected)
    } finally {
      setStatus(null)
    }
    if (isValid) {
      setStatus(
        <AddressValid>
          <Check />
          <Trans>Address is within delivery zone</Trans>
        </AddressValid>,
      )
    } else {
      setStatus(
        <ZoneWarning>
          <Warning />
          <Trans>Address is not within any delivery zone</Trans>
        </ZoneWarning>,
      )
    }
  }

  const getPreviousLocations = () => {
    if (!createOrder.vamCustomer) {
      if (createOrder.fakeAddress) {
        return [createOrder.fakeAddress]
      }
      return []
    }
    const { locations } = createOrder.vamCustomer
    return locations
  }

  return (
    <div hidden={!props.isVisible}>
      <InputLine>
        <Flex>
          <AddressSuggest
            fixedLocations={getPreviousLocations()}
            fixedLocationsLabel={i18n.t`Previous addresses`}
            selectedLocation={
              createOrder.deliveryAddress || createOrder.fakeAddress || null
            }
            onSelect={onSuggestSelect}
            onClear={createOrder.clearLocation}
            inputPlaceholder={i18n.t`Deliver to address`.toLocaleLowerCase()}
          />
          {status}
        </Flex>
      </InputLine>
    </div>
  )
})

function useAddressValidation(branchId: ID) {
  const client = useApolloClient()
  return async (address: IAddressWithGps) => {
    if (!address.gps) {
      return false
    }
    const { latitude, longitude } = address.gps
    const variables: GValidateLocation.Variables = {
      branchId,
      latitude,
      longitude,
    }
    const result: ApolloQueryResult<
      GValidateLocation.Query
    > = await client.query({
      query: ValidateLocationQuery,
      variables,
    })

    return result.data.resolveLocation.inDeliveryLocation
  }
}
